<template>
    <div id='box'>
      <div class="top">
        <van-search
          v-model="value"
          placeholder="请输入搜索关键词"
          show-action
          @blur="getsearch"
          left-icon=""
          shape="round"/>
      </div>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="list">
          <div class="item" v-for="(item,index) in data" :key="index"  @click="$router.push({path:'/goodsDetail',query:{id:item.goods_id}})">
            <img :src="item.original_img" alt="" class="img">
            <div class="right">
              <p class="title">{{item.goods_name}}</p>
              <p class='guige'>原价:￥{{item.shop_price}}</p>
              <!-- <span class="icon">包邮</span> -->
              <div class="bottom">
                <p>会员价:<span>￥{{item.shop_price}}</span></p>
                <p>已售：{{item.sales_sum}} 件</p>
              </div>
            </div>
          </div>
        </div>

      </van-list>
      <!-- <div v-show="nomore" class="nomore">暂无数据</div> -->
      <!-- 前往购物车 -->
      <img src="@/assets/images/mall/mall_cart.png" alt="" class="cart" @click="$router.push({path:'/cart'})">
    </div>
</template>

<script>
import { Search, Loading, List, Toast  } from 'vant';
import { getsearch } from '@/axios/getData';
export default {
    props: {

    },
    data() {
      return {
          data:[0],        // 搜索的列表数据
          keyword:'',     // 搜索的关键字
          value:'',

          loading: false,
          finished: false,
          page:1,
          nomore:false
        };
    },
    methods: {
      async getdata(){
        var res = await getsearch({keyword:this.keyword, page: this.page});
          this.loading = false;
    
          if( this.page==1 ) {
            this.data = res.data.goods_list.list;
            if(res.data.goods_list.length==0){
              // this.nomore = true;
              this.finished = true;
            }
          } else {
            this.data = this.data.concat(res.data.goods_list.list);
          }
          this.page++;
          if(res.data.goods_list.count<=0){
            this.finished = true
            console.log(111,res.data.goods_list.count)
          }       
        // } else {
        //   this.finished = true;
        //   this.data = 1
        // }
      },
      getsearch(){
        if( this.value == this.keyword ) return;
        this.keyword = this.value;


        this.getstorage()

        this.finished = false;
        this.page = 1;
        this.onLoad();
      },
      getstorage(){
        var key_list = localStorage.getItem('key');
        if(key_list){
          var arr = JSON.parse(key_list);
          for(let i = 0;i<arr.length;i++){
            if( this.keyword == arr[i] ) return;    //  过滤重复
          }
          arr.unshift(this.keyword || '全部商品')
          if(arr.length>3) arr.splice(3);
          localStorage.setItem('key',JSON.stringify(arr));
        } else {
          var arr = [];
          arr[0] = this.keyword || '全部商品'
          localStorage.setItem('key',JSON.stringify(arr));
        }
        // console.log(JSON.parse(localStorage.getItem('key')))
      },
      onLoad(){
        if( this.loading ){
          getsearch({keyword:this.keyword,page:this.page}).then((res)=>{
            if(this.page>1&&res.data.goods_list){
              this.data = this.data.concat(res.data.goods_list.list);
            } else {
              this.data = res.data.goods_list.list;
            }
            this.loading = false;
            this.page++;
            if(res.data.goods_list.count<=0||!res.data.goods_lis){
              this.finished = true
            }
          });
        }
      }
    },
    components: {
      vanSearch: Search,
      vanList: List
    },
    computed: {

    },
    created() {
      this.keyword = this.$route.query.keyword=='全部商品'?'':this.$route.query.keyword;
      this.getstorage();
      // this.getdata()
    },
    mounted() {

    },
};
</script>

<style scoped lang="scss">
/deep/ .van-search{
  background: #F5F5F5 !important;
}
/deep/ .van-cell{
  background: #fff;
  border-radius: 20pt;
  padding-left:15pt;
}
.top{
  background: #F5F5F5;
}
.list{
  .item{
    padding:20pt;
    display: flex;
    .right{
      padding-top:10pt;
      width:60%;
      .icon{
        padding:1.5pt;
        border-radius: 4pt;
        color:#FF4C24;
        border:1pt solid #FF4C24;
        font-size: 7pt;
      }
      .guige{
        display: block;
        margin:5pt 0;
        color:#999;
        text-decoration: line-through;
        font-size: 9pt;
      }
      .title{
        font-size: 10pt;
        color:#333;
      }
      .bottom{
        // display: flex;
        padding-top:5pt;
        // justify-content: space-between;
        align-items: center;
        color:#999;
        span{
          color:#FF4C24;
          font-size: 15pt;
          font-weight: 600;
        }
        .cart{
          width:25pt;
          height:25pt;
        }
      }
    }
    .img{
      margin-right:15pt;
      width:100pt;
      height:100pt;
    }
  }
}
.nomore{
  padding:30pt 0;
  color:#999;
  font-size: 13pt;
  text-align: center;
  // height:100vh;
}

/* 前往购物车的图标 */
.cart{
  position: fixed;
  top:80%;
  right:5%;
  width:40pt;
  height:40pt;
}
</style>



